:root {
  --picker-text: black;
}

#picker {
  max-width: -moz-fit-content;
  max-width: fit-content;
  border-radius: 12px;
  padding: 1rem;
  transition: background .2s ease-out;
}

#picker table {
  caption-side: bottom;
  border-spacing: 0 3px;
  transition: background .2s ease-out;
}

#picker td, #picker th, #picker label, #picker caption, #picker caption a, #picker legend {
  color: var(--picker-text);
}

caption {
  padding-block: 1rem;
}

td, th {
  font-size: 1.4rem;
}

thead th {
  font-size: .8rem;
  font-weight: lighter;
}

tr th {
  font-weight: normal;
}

#picker-preferences {
  gap: 4rem;
  padding-inline: 2rem;
  display: flex;
}

#picker-preferences fieldset {
  gap: 2rem;
  display: flex;
}

.input {
  width: min-content;
  flex-direction: column;
  display: flex;
}

.bar-name {
  text-align: start;
  align-content: center;
  padding-inline-end: .5em;
  transition: color .2s ease-out;
}

.bar:first-of-type td:first-of-type {
  border-radius: 8px 0 0;
}

.bar:first-of-type th:last-of-type {
  border-radius: 0 8px 0 0;
}

.bar:last-of-type td:first-of-type {
  border-radius: 0 0 0 8px;
}

.bar:last-of-type th:last-of-type {
  border-radius: 0 0 8px;
}

.palette-swatches {
  height: 3rem;
  flex-wrap: wrap;
  display: flex;
}

.palette-swatch {
  height: 100%;
  aspect-ratio: 1;
}

.palette {
  align-items: center;
  gap: 3rem;
  display: flex;
}

